.profile__info-avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  position: relative;
}



.profile__info .hoverImg {
  position: absolute;
  left: 0;
  top: 0;
  display: none;
}

.profile__info:hover .hoverImg {
  display: block;
}
